

'use client';

import * as React from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from "@/components/ui/card";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import { Textarea } from "@/components/ui/textarea";
import { FileText, PlusCircle, Trash2, ArrowUp, ArrowDown, Users, Gavel, Mic, Info, Edit, Plus, Minus, RefreshCcw } from "lucide-react";
import { useToast } from "@/hooks/use-toast";
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from "@/components/ui/accordion";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
  DialogClose,
} from "@/components/ui/dialog";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Badge } from "@/components/ui/badge";
import { cn } from "@/lib/utils";
import type { User, DebateCase, CaseContention, PracticeRound, Ballot } from "@/lib/types";
import { useAuth } from "@/contexts/auth-context";
import { MultiSelect } from "@/components/ui/multi-select";
import { Separator } from "@/components/ui/separator";

const DEBATE_CASES_STORAGE_KEY = 'work-session-debate-cases';
const PRACTICE_ROUNDS_STORAGE_KEY = 'work-session-practice-rounds';

type DebateFormat = "LD" | "PF" | "CX" | "WSD";

export default function DebateEventsPage() {
  const { user, allUsers } = useAuth();
  const { toast } = useToast();

  const [cases, setCases] = React.useState<DebateCase[]>(() => {
    if (typeof window === 'undefined') return [];
    const stored = localStorage.getItem(DEBATE_CASES_STORAGE_KEY);
    return stored ? JSON.parse(stored) : [];
  });
  
  const [practiceRounds, setPracticeRounds] = React.useState<PracticeRound[]>(() => {
    if (typeof window === 'undefined') return [];
    const stored = localStorage.getItem(PRACTICE_ROUNDS_STORAGE_KEY);
    return stored ? JSON.parse(stored) : [];
  });

  const [isCaseDialogOpen, setIsCaseDialogOpen] = React.useState(false);
  const [caseToEdit, setCaseToEdit] = React.useState<DebateCase | null>(null);

  const [isRoundDialogOpen, setIsRoundDialogOpen] = React.useState(false);
  const [roundToEdit, setRoundToEdit] = React.useState<PracticeRound | null>(null);

  React.useEffect(() => {
    localStorage.setItem(DEBATE_CASES_STORAGE_KEY, JSON.stringify(cases));
  }, [cases]);
  
  React.useEffect(() => {
    localStorage.setItem(PRACTICE_ROUNDS_STORAGE_KEY, JSON.stringify(practiceRounds));
  }, [practiceRounds]);

  const handleSaveCase = (caseData: Omit<DebateCase, 'id' | 'ownerId'>) => {
    if (!user) return;
    if (caseToEdit) {
      const updatedCase: DebateCase = { ...caseToEdit, ...caseData };
      setCases(prev => prev.map(c => c.id === caseToEdit.id ? updatedCase : c));
      toast({ title: "Case Updated" });
    } else {
      const newCase: DebateCase = {
        id: `case-${Date.now()}`,
        ownerId: user.id,
        ...caseData,
      };
      setCases(prev => [newCase, ...prev]);
      toast({ title: "Case Created" });
    }
    setIsCaseDialogOpen(false);
    setCaseToEdit(null);
  };
  
  const handleDeleteCase = (id: string) => {
    setCases(prev => prev.filter(c => c.id !== id));
    toast({ title: "Case Deleted", variant: "destructive" });
  };
  
  const handleSaveRound = (roundData: Omit<PracticeRound, 'id'>) => {
     if (roundToEdit) {
      const updatedRound: PracticeRound = { ...roundToEdit, ...roundData };
      setPracticeRounds(prev => prev.map(r => r.id === roundToEdit.id ? updatedRound : r));
      toast({ title: "Practice Round Updated" });
    } else {
      const newRound: PracticeRound = {
        id: `round-${Date.now()}`,
        ...roundData,
      };
      setPracticeRounds(prev => [newRound, ...prev]);
      toast({ title: "Practice Round Created" });
    }
    setIsRoundDialogOpen(false);
    setRoundToEdit(null);
  }
  
  const handleDeleteRound = (id: string) => {
    setPracticeRounds(prev => prev.filter(r => r.id !== id));
    toast({ title: "Practice Round Deleted", variant: "destructive" });
  };
  
  const handleBallotUpdate = (roundId: string, ballot: Ballot) => {
    setPracticeRounds(prev => prev.map(r => r.id === roundId ? { ...r, ballot } : r));
    toast({title: "Ballot Submitted"});
  }

  const handleFlowUpdate = (roundId: string, flow: { aff?: string[][], neg?: string[][] }) => {
     setPracticeRounds(prev => prev.map(r => r.id === roundId ? { ...r, flow: {...r.flow, ...flow} } : r));
  }

  return (
    <div className="flex flex-col gap-6">
      <div>
        <h1 className="text-3xl font-bold font-headline">Debate Events</h1>
        <p className="text-muted-foreground">
          Construct cases and run practice rounds.
        </p>
      </div>

      <Tabs defaultValue="case-creator" className="w-full">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="case-creator">Case Creator</TabsTrigger>
          <TabsTrigger value="practice-rounds">Practice Rounds</TabsTrigger>
        </TabsList>
        <TabsContent value="case-creator" className="mt-4 space-y-6">
          <Card>
            <CardHeader>
              <div className="flex items-center justify-between">
                <CardTitle>My Cases</CardTitle>
                <Button onClick={() => { setCaseToEdit(null); setIsCaseDialogOpen(true); }}>
                  <PlusCircle className="mr-2" /> New Case
                </Button>
              </div>
              <CardDescription>Create, view, and manage your debate cases.</CardDescription>
            </CardHeader>
            <CardContent>
              {cases.length > 0 ? (
                <Accordion type="single" collapsible className="w-full">
                  {cases.filter(c => c.ownerId === user?.id).map((c) => (
                    <AccordionItem value={c.id} key={c.id}>
                      <AccordionTrigger>
                        <div className="flex-1 text-left">
                          {c.title}
                          <Badge variant="secondary" className="ml-2">{c.type}</Badge>
                        </div>
                      </AccordionTrigger>
                      <AccordionContent className="space-y-4">
                        <div className="prose prose-sm dark:prose-invert max-w-none">
                            <p className="whitespace-pre-wrap">{c.constructive}</p>
                            <Separator className="my-4" />
                            <h4 className="font-semibold">Contentions</h4>
                            <ul className="list-disc pl-5">
                                {c.contentions.map(con => <li key={con.id}>{con.title}</li>)}
                            </ul>
                        </div>
                        <div className="flex gap-2">
                          <Button size="sm" variant="outline" onClick={() => { setCaseToEdit(c); setIsCaseDialogOpen(true); }}>
                            <Edit className="mr-2" /> Edit
                          </Button>
                           <AlertDialog>
                              <AlertDialogTrigger asChild><Button size="sm" variant="destructive"><Trash2 className="mr-2" /> Delete</Button></AlertDialogTrigger>
                              <AlertDialogContent>
                                <AlertDialogHeader><AlertDialogTitle>Delete this case?</AlertDialogTitle><AlertDialogDescription>This action cannot be undone.</AlertDialogDescription></AlertDialogHeader>
                                <AlertDialogFooter>
                                  <AlertDialogCancel>Cancel</AlertDialogCancel>
                                  <AlertDialogAction onClick={() => handleDeleteCase(c.id)}>Delete</AlertDialogAction>
                                </AlertDialogFooter>
                              </AlertDialogContent>
                            </AlertDialog>
                        </div>
                      </AccordionContent>
                    </AccordionItem>
                  ))}
                </Accordion>
              ) : (
                <div className="text-center text-muted-foreground py-8">No cases created yet.</div>
              )}
            </CardContent>
          </Card>
        </TabsContent>
        
        <TabsContent value="practice-rounds" className="mt-4 space-y-6">
           <Card>
            <CardHeader>
              <div className="flex items-center justify-between">
                <CardTitle>My Practice Rounds</CardTitle>
                <Button onClick={() => { setRoundToEdit(null); setIsRoundDialogOpen(true); }}>
                  <PlusCircle className="mr-2" /> New Round
                </Button>
              </div>
              <CardDescription>Manage and participate in practice rounds.</CardDescription>
            </CardHeader>
            <CardContent>
               {practiceRounds.length > 0 ? (
                <Accordion type="single" collapsible className="w-full">
                  {practiceRounds.map((round) => (
                    <PracticeRoundItem key={round.id} round={round} onBallotUpdate={handleBallotUpdate} onFlowUpdate={handleFlowUpdate} allUsers={allUsers} onDelete={handleDeleteRound} onEdit={() => { setRoundToEdit(round); setIsRoundDialogOpen(true); }} />
                  ))}
                </Accordion>
              ) : (
                <div className="text-center text-muted-foreground py-8">No practice rounds created yet.</div>
              )}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
      
      <CaseDialog
        isOpen={isCaseDialogOpen}
        onOpenChange={setIsCaseDialogOpen}
        onSave={handleSaveCase}
        caseToEdit={caseToEdit}
      />
      <RoundDialog
        isOpen={isRoundDialogOpen}
        onOpenChange={setIsRoundDialogOpen}
        onSave={handleSaveRound}
        roundToEdit={roundToEdit}
        allUsers={allUsers}
       />
    </div>
  );
}

function CaseDialog({ isOpen, onOpenChange, onSave, caseToEdit }: { isOpen: boolean, onOpenChange: (open: boolean) => void, onSave: (data: Omit<DebateCase, 'id'|'ownerId'>) => void, caseToEdit: DebateCase | null }) {
  const [title, setTitle] = React.useState('');
  const [type, setType] = React.useState<DebateFormat>('LD');
  const [constructive, setConstructive] = React.useState('');
  const [contentions, setContentions] = React.useState<CaseContention[]>([]);
  const { toast } = useToast();

  React.useEffect(() => {
    if (caseToEdit) {
      setTitle(caseToEdit.title);
      setType(caseToEdit.type);
      setConstructive(caseToEdit.constructive);
      setContentions(caseToEdit.contentions);
    } else {
      setTitle('');
      setType('LD');
      setConstructive('');
      setContentions([]);
    }
  }, [caseToEdit, isOpen]);

  const handleSave = () => {
    if (!title || !type) {
      toast({ variant: 'destructive', title: 'Title and type are required.' });
      return;
    }
    onSave({ title, type, constructive, contentions });
  };
  
  const addContention = () => {
    setContentions(prev => [...prev, { id: `contention-${Date.now()}`, title: `Contention ${prev.length + 1}`, text: '' }]);
  }
  
  const updateContention = (id: string, field: 'title' | 'text', value: string) => {
    setContentions(prev => prev.map(c => c.id === id ? {...c, [field]: value} : c));
  }

  const removeContention = (id: string) => {
    setContentions(prev => prev.filter(c => c.id !== id));
  }
  
  const moveContention = (index: number, direction: 'up' | 'down') => {
    const newIndex = direction === 'up' ? index - 1 : index + 1;
    if (newIndex < 0 || newIndex >= contentions.length) return;
    const newContentions = [...contentions];
    const temp = newContentions[index];
    newContentions[index] = newContentions[newIndex];
    newContentions[newIndex] = temp;
    setContentions(newContentions);
  }

  return (
    <Dialog open={isOpen} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-4xl h-[90vh]">
        <DialogHeader>
          <DialogTitle>{caseToEdit ? 'Edit Case' : 'Create New Case'}</DialogTitle>
        </DialogHeader>
        <div className="grid md:grid-cols-2 gap-6 h-full overflow-hidden py-4">
          <div className="space-y-4 flex flex-col">
            <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                    <Label htmlFor="case-title">Case Title</Label>
                    <Input id="case-title" value={title} onChange={e => setTitle(e.target.value)} />
                </div>
                <div className="space-y-2">
                    <Label htmlFor="case-type">Debate Type</Label>
                    <Select value={type} onValueChange={(v: DebateFormat) => setType(v)}>
                        <SelectTrigger id="case-type"><SelectValue/></SelectTrigger>
                        <SelectContent>
                            <SelectItem value="LD">Lincoln Douglas (LD)</SelectItem>
                            <SelectItem value="PF">Public Forum (PF)</SelectItem>
                            <SelectItem value="CX">Cross Examination (CX)</SelectItem>
                            <SelectItem value="WSD">World Schools Debate (WSD)</SelectItem>
                        </SelectContent>
                    </Select>
                </div>
            </div>
            <div className="space-y-2 flex-grow flex flex-col">
              <Label htmlFor="case-constructive">Constructive</Label>
              <Textarea id="case-constructive" value={constructive} onChange={e => setConstructive(e.target.value)} className="h-full flex-grow resize-none" />
            </div>
          </div>
          <div className="space-y-4 flex flex-col">
            <div className="flex items-center justify-between">
                <h3 className="text-lg font-medium">Swappable Contentions</h3>
                <Button variant="outline" size="sm" onClick={addContention}><PlusCircle className="mr-2"/> Add</Button>
            </div>
            <div className="flex-grow overflow-y-auto space-y-4 pr-4 -mr-4">
                {contentions.map((contention, index) => (
                    <Card key={contention.id}>
                        <CardHeader className="flex flex-row items-center justify-between p-3">
                            <Input value={contention.title} onChange={e => updateContention(contention.id, 'title', e.target.value)} className="h-8 text-sm" />
                            <div className="flex gap-1">
                                <Button size="icon" variant="ghost" className="h-8 w-8" onClick={() => moveContention(index, 'up')} disabled={index === 0}><ArrowUp /></Button>
                                <Button size="icon" variant="ghost" className="h-8 w-8" onClick={() => moveContention(index, 'down')} disabled={index === contentions.length - 1}><ArrowDown /></Button>
                                <Button size="icon" variant="ghost" className="h-8 w-8 text-destructive" onClick={() => removeContention(contention.id)}><Trash2/></Button>
                            </div>
                        </CardHeader>
                        <CardContent className="p-3 pt-0">
                            <Textarea value={contention.text} onChange={e => updateContention(contention.id, 'text', e.target.value)} placeholder="Contention text..."/>
                        </CardContent>
                    </Card>
                ))}
                 {contentions.length === 0 && <p className="text-sm text-muted-foreground text-center pt-8">No contentions added.</p>}
            </div>
          </div>
        </div>
        <DialogFooter>
          <DialogClose asChild><Button variant="outline">Cancel</Button></DialogClose>
          <Button onClick={handleSave}>Save Case</Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}

function RoundDialog({ isOpen, onOpenChange, onSave, roundToEdit, allUsers }: { isOpen: boolean, onOpenChange: (open: boolean) => void, onSave: (data: Omit<PracticeRound, 'id'>) => void, roundToEdit: PracticeRound | null, allUsers: User[] }) {
    const [topic, setTopic] = React.useState('');
    const [type, setType] = React.useState<DebateFormat>('LD');
    const [judges, setJudges] = React.useState<string[]>([]);
    const [affTeam, setAffTeam] = React.useState<string[]>([]);
    const [negTeam, setNegTeam] = React.useState<string[]>([]);
    const { toast } = useToast();

    const availableUsers = React.useMemo(() => {
        return allUsers.map(u => ({label: u.name, value: u.id}));
    }, [allUsers]);

    const maxTeamSize = React.useMemo(() => {
        switch (type) {
            case 'PF':
            case 'CX':
                return 2;
            case 'WSD':
                return 5;
            case 'LD':
            default:
                return 1;
        }
    }, [type]);

    React.useEffect(() => {
        if (roundToEdit) {
            setTopic(roundToEdit.topic);
            setType(roundToEdit.type);
            setJudges(roundToEdit.judges);
            setAffTeam(roundToEdit.affTeam || []);
            setNegTeam(roundToEdit.negTeam || []);
        } else {
            setTopic('');
            setType('LD');
            setJudges([]);
            setAffTeam([]);
            setNegTeam([]);
        }
    }, [roundToEdit, isOpen]);
    
    React.useEffect(() => {
        // Reset teams if they exceed max size for the new format
        if (affTeam.length > maxTeamSize) setAffTeam(affTeam.slice(0, maxTeamSize));
        if (negTeam.length > maxTeamSize) setNegTeam(negTeam.slice(0, maxTeamSize));
    }, [type, maxTeamSize, affTeam, negTeam]);

    const handleSave = () => {
        if (!topic.trim()) {
            toast({
                variant: 'destructive',
                title: 'Topic Required',
                description: 'Please enter a topic/resolution for the round.',
            });
            return;
        }
        const initialFlow = { aff: Array(4).fill(Array(4).fill('')), neg: Array(4).fill(Array(4).fill('')) };
        const allParticipants = [...new Set([...affTeam, ...negTeam, ...judges])];
        onSave({ topic, type, participants: allParticipants, judges, ballot: roundToEdit?.ballot, flow: roundToEdit?.flow || initialFlow, affTeam, negTeam });
    };

    const handleFlip = () => {
        const currentAff = [...affTeam];
        const currentNeg = [...negTeam];
        setAffTeam(currentNeg);
        setNegTeam(currentAff);
    }
    
    const TeamSelect = ({ label, value, onChange, max, disabledIds }: { label: string, value: string[], onChange: (value: string[]) => void, max: number, disabledIds: string[] }) => {
        const options = availableUsers.filter(u => !disabledIds.includes(u.value) || value.includes(u.value));
        if (max === 1) {
            return (
                 <Select value={value[0] || ''} onValueChange={(v) => onChange(v ? [v] : [])}>
                    <SelectTrigger><SelectValue placeholder={`Select ${label}`} /></SelectTrigger>
                    <SelectContent>
                        {options.map(p => (
                            <SelectItem key={p.value} value={p.value}>{p.label}</SelectItem>
                        ))}
                    </SelectContent>
                </Select>
            )
        }
        return (
            <MultiSelect options={options} value={value} onValueChange={onChange} placeholder={`Select ${label} (up to ${max})...`} maxCount={max} />
        )
    }

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent>
                <DialogHeader>
                    <DialogTitle>{roundToEdit ? 'Edit' : 'Create'} Practice Round</DialogTitle>
                </DialogHeader>
                <div className="space-y-4 py-4">
                    <div className="space-y-2">
                        <Label htmlFor="round-topic">Topic/Resolution</Label>
                        <Input id="round-topic" value={topic} onChange={e => setTopic(e.target.value)} />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="round-type">Debate Type</Label>
                         <Select value={type} onValueChange={(v: DebateFormat) => setType(v)}>
                            <SelectTrigger id="round-type"><SelectValue/></SelectTrigger>
                            <SelectContent>
                                <SelectItem value="LD">Lincoln Douglas (LD)</SelectItem>
                                <SelectItem value="PF">Public Forum (PF)</SelectItem>
                                <SelectItem value="CX">Cross Examination (CX)</SelectItem>
                                <SelectItem value="WSD">World Schools Debate (WSD)</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>

                    <div className="flex items-center gap-2">
                         <div className="flex-1 space-y-2">
                            <Label>Affirmative</Label>
                            <TeamSelect label="Affirmative" value={affTeam} onChange={setAffTeam} max={maxTeamSize} disabledIds={negTeam} />
                        </div>
                        <Button size="icon" variant="outline" className="mt-8 flex-shrink-0" onClick={handleFlip}><RefreshCcw className="h-4 w-4"/></Button>
                         <div className="flex-1 space-y-2">
                            <Label>Negative</Label>
                            <TeamSelect label="Negative" value={negTeam} onChange={setNegTeam} max={maxTeamSize} disabledIds={affTeam} />
                        </div>
                    </div>
                    
                     <div className="space-y-2">
                        <Label>Judges</Label>
                        <MultiSelect options={availableUsers} value={judges} onValueChange={setJudges} placeholder="Select judge(s)..."/>
                    </div>
                </div>
                <DialogFooter>
                    <DialogClose asChild><Button variant="outline">Cancel</Button></DialogClose>
                    <Button onClick={handleSave}>Save Round</Button>
                </DialogFooter>
            </DialogContent>
        </Dialog>
    );
}

function PracticeRoundItem({ round, onBallotUpdate, onFlowUpdate, allUsers, onDelete, onEdit }: { round: PracticeRound, onBallotUpdate: (id: string, ballot: Ballot) => void, onFlowUpdate: (id: string, flow: { aff?: string[][], neg?: string[][] }) => void, allUsers: User[], onDelete: (id: string) => void, onEdit: () => void }) {
    const { user } = useAuth();
    const isJudge = user ? round.judges.includes(user.id) : false;
    
    const affTeam = (round.affTeam || []).map(id => allUsers.find(u => u.id === id)?.name).filter(Boolean);
    const negTeam = (round.negTeam || []).map(id => allUsers.find(u => u.id === id)?.name).filter(Boolean);
    
    return (
        <AccordionItem value={round.id}>
            <AccordionTrigger>
                <div className="flex-1 text-left">
                    {round.topic}
                    <Badge variant="secondary" className="ml-2">{round.type}</Badge>
                </div>
            </AccordionTrigger>
            <AccordionContent className="space-y-4">
                <div className="grid md:grid-cols-2 gap-4 text-sm">
                    <div>
                        <h4 className="font-semibold flex items-center gap-2 mb-1"><Users className="h-4 w-4"/> Sides</h4>
                        <div className="flex flex-col gap-1 text-muted-foreground">
                            <span><Badge>AFF</Badge> {affTeam.join(', ') || 'Not set'}</span>
                            <span><Badge variant="destructive">NEG</Badge> {negTeam.join(', ') || 'Not set'}</span>
                        </div>
                    </div>
                     <div className="space-y-1">
                        <h4 className="font-semibold flex items-center gap-2"><Gavel className="h-4 w-4"/> Judges</h4>
                        <p className="text-muted-foreground">{round.judges.map(pId => allUsers.find(u => u.id === pId)?.name).join(', ')}</p>
                    </div>
                </div>
                 <div className="flex justify-between items-center gap-2">
                    <div className="flex gap-2">
                        <Button size="sm" variant="outline" onClick={onEdit}><Edit className="mr-2"/> Edit</Button>
                        <AlertDialog>
                            <AlertDialogTrigger asChild><Button size="sm" variant="destructive"><Trash2 className="mr-2"/> Delete</Button></AlertDialogTrigger>
                            <AlertDialogContent>
                                <AlertDialogHeader><AlertDialogTitle>Delete this round?</AlertDialogTitle></AlertDialogHeader>
                                <AlertDialogFooter>
                                    <AlertDialogCancel>Cancel</AlertDialogCancel>
                                    <AlertDialogAction onClick={() => onDelete(round.id)}>Delete</AlertDialogAction>
                                </AlertDialogFooter>
                            </AlertDialogContent>
                        </AlertDialog>
                    </div>
                 </div>
                 <Separator />
                 <Accordion type="single" collapsible className="w-full" defaultValue="ballot">
                    {round.type === "LD" ? (
                        <>
                             <AccordionItem value="aff-flow">
                                <AccordionTrigger>Aff Flow</AccordionTrigger>
                                <AccordionContent>
                                <FlowSheet flow={round.flow?.aff} onUpdate={(newFlow) => onFlowUpdate(round.id, { aff: newFlow })} />
                                </AccordionContent>
                            </AccordionItem>
                             <AccordionItem value="neg-flow">
                                <AccordionTrigger>Neg Flow</AccordionTrigger>
                                <AccordionContent>
                                <FlowSheet flow={round.flow?.neg} onUpdate={(newFlow) => onFlowUpdate(round.id, { neg: newFlow })} />
                                </AccordionContent>
                            </AccordionItem>
                        </>
                    ) : (
                         <AccordionItem value="flow">
                            <AccordionTrigger>Flow</AccordionTrigger>
                            <AccordionContent>
                               <FlowSheet flow={round.flow?.aff} onUpdate={(newFlow) => onFlowUpdate(round.id, { aff: newFlow })} />
                            </AccordionContent>
                        </AccordionItem>
                    )}
                    <AccordionItem value="ballot">
                        <AccordionTrigger>Ballot</AccordionTrigger>
                        <AccordionContent>
                            <BallotForm ballot={round.ballot} isJudge={isJudge} onUpdate={(newBallot) => onBallotUpdate(round.id, newBallot)} />
                        </AccordionContent>
                    </AccordionItem>
                </Accordion>
            </AccordionContent>
        </AccordionItem>
    );
}

function BallotForm({ ballot, isJudge, onUpdate }: { ballot?: Ballot, isJudge: boolean, onUpdate: (ballot: Ballot) => void }) {
    const [winner, setWinner] = React.useState(ballot?.winner || '');
    const [rfd, setRfd] = React.useState(ballot?.rfd || '');
    const [affPoints, setAffPoints] = React.useState(ballot?.affPoints || '');
    const [negPoints, setNegPoints] = React.useState(ballot?.negPoints || '');
    const [affComments, setAffComments] = React.useState(ballot?.affComments || '');
    const [negComments, setNegComments] = React.useState(ballot?.negComments || '');
    
    const handleSave = () => {
        onUpdate({ winner, rfd, affPoints, negPoints, affComments, negComments });
    }

    if (!isJudge && !ballot) {
        return <p className="text-sm text-muted-foreground text-center py-4">The ballot has not been filled out yet.</p>;
    }
    
    return (
        <div className="space-y-4">
            <div className="grid md:grid-cols-3 gap-4">
                <div className="space-y-2">
                    <Label>Winner</Label>
                    <Input value={winner} onChange={e => setWinner(e.target.value)} readOnly={!isJudge} />
                </div>
                 <div className="space-y-2">
                    <Label>Aff Speaker Points</Label>
                    <Input value={affPoints} onChange={e => setAffPoints(e.target.value)} readOnly={!isJudge} />
                </div>
                 <div className="space-y-2">
                    <Label>Neg Speaker Points</Label>
                    <Input value={negPoints} onChange={e => setNegPoints(e.target.value)} readOnly={!isJudge} />
                </div>
            </div>
             <div className="space-y-2">
                <Label>Reason for Decision (RFD)</Label>
                <Textarea value={rfd} onChange={e => setRfd(e.target.value)} readOnly={!isJudge} />
            </div>
            <div className="grid md:grid-cols-2 gap-4">
                <div className="space-y-2">
                    <Label>Comments for Affirmative</Label>
                    <Textarea value={affComments} onChange={e => setAffComments(e.target.value)} readOnly={!isJudge} />
                </div>
                <div className="space-y-2">
                    <Label>Comments for Negative</Label>
                    <Textarea value={negComments} onChange={e => setNegComments(e.target.value)} readOnly={!isJudge} />
                </div>
            </div>
            {isJudge && <Button onClick={handleSave}>Submit Ballot</Button>}
        </div>
    );
}

function FlowSheet({ flow, onUpdate }: { flow?: string[][], onUpdate: (flow: string[][]) => void }) {
    const defaultFlow = Array(4).fill(null).map(() => Array(4).fill(''));
    const [currentFlow, setCurrentFlow] = React.useState(flow || defaultFlow);

    const handleCellChange = (rowIndex: number, colIndex: number, value: string) => {
        const newFlow = currentFlow.map((row, rIdx) => 
            rIdx === rowIndex ? row.map((cell, cIdx) => cIdx === colIndex ? value : cell) : row
        );
        setCurrentFlow(newFlow);
        onUpdate(newFlow);
    };

    const columnHeaders = ["Constructive", "Rebuttal 1", "Rebuttal 2", "Final Focus/Crystallization"];

    return (
        <div className="overflow-x-auto">
            <div className="grid grid-cols-4 gap-2 min-w-[800px]">
                {columnHeaders.map((header, colIndex) => (
                    <div key={header} className="font-bold text-center text-sm p-2 bg-muted rounded-t-md">{header}</div>
                ))}
                {currentFlow.map((row, rowIndex) => (
                    <React.Fragment key={rowIndex}>
                        {row.map((cell, colIndex) => (
                            <Textarea
                                key={`${rowIndex}-${colIndex}`}
                                value={cell}
                                onChange={(e) => handleCellChange(rowIndex, colIndex, e.target.value)}
                                className="h-24 resize-none"
                                placeholder=""
                            />
                        ))}
                    </React.Fragment>
                ))}
            </div>
        </div>
    );
}
